import Vue from 'vue'
import Router from 'vue-router'
import Apply from '@/pages/apply'
import element from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
Vue.use(Router)
Vue.use(element)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Apply',
      component: Apply
    }
  ]
})



