<template>
<div>
  <el-tabs type="border-card">
    <el-tab-pane label="基本信息">
      <el-form :inline="true" :model="basicForm" ref="basicForm" :rules="rules"  class="demo-form-inline log-input">
        <el-form-item  class="" label="费用主题" prop="theme" :label-width="labelWidth">
          <el-input v-model="basicForm.theme"  placeholder="请填写费用主题"></el-input>
        </el-form-item>
        <el-form-item label="费用编号" prop="number" :label-width="labelWidth">
          <el-input v-model="basicForm.number" placeholder="请填写费用编号"></el-input>
        </el-form-item>
        <el-form-item label="归属部门" prop="dep" :label-width="labelWidth">
          <el-input v-model="basicForm.dep" placeholder="请填写归属部门"></el-input>
        </el-form-item>
      </el-form>
    </el-tab-pane>
      <el-row class="little-nav">
        <el-col :span="18">报销详情</el-col>
        <el-col :span="6" class="text-right"><el-button type="primary" @click="add" icon="plus" class=""> 新 增 </el-button></el-col>
      </el-row>
      <div class="text-center">
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            type="index"
            label="序号"
            width="90">
          </el-table-column>
          <el-table-column
            prop="subject"
            label="费用科目"
            width="360">
          </el-table-column>
          <el-table-column
            prop="money"
            label="金额(元)"
            width="240">
          </el-table-column>
          <el-table-column
            prop="date"
            label="发生时间"
            width="300">
          </el-table-column>
          <el-table-column
            prop="remark"
            label="备注"
            >
          </el-table-column>
          <el-table-column
            label="操作"
            width="200">
            <template scope="scope">
              <el-button type="info" @click="edit(scope.row,scope.$index)" size="small" icon="edit">编辑</el-button>
              <el-button type="danger" @click="deleteDta(scope.row,scope.$index)" size="small" icon="delete">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-row class="total-money">
          <el-col :span="6">
            总金额：
          </el-col>
          <el-col :span="18" style="text-align: left">
            {{total}}
          </el-col>
        </el-row>

      </div>
    <div class="btn-box text-center">
      <el-button type="primary" size="large" @click="submit('basicForm')">提 交</el-button>
      <el-button type="primary" size="large" @click="pass()">通过</el-button>
      <el-button type="primary" size="large" @click="nopass()">不通过</el-button>
    </div>
  </el-tabs>
  <el-dialog title="新增" :visible.sync="addFormVisible">
    <el-form :model="addForm"  ref="addForm"  :rules="rules"  :inline="true"  class="demo-form-inline line-form">
      <el-form-item   label="费用科目" :label-width="formLabe2Width" prop="subject">
        <el-input v-model="addForm.subject"  auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item   label="金额" :label-width="formLabe2Width" prop="money">
        <el-input v-model="addForm.money"  auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item   label="发生时间"   :label-width="formLabe2Width" prop="date">
        <el-date-picker type="date" @change="dateChangeAdd" placeholder="选择日期" v-model="addForm.date" style="width: 100%;"></el-date-picker>
      </el-form-item>
      <el-form-item   label="备注" :label-width="formLabe2Width">
        <el-input v-model="addForm.remark"  auto-complete="off"></el-input>
      </el-form-item>

    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="addFormVisible = false">取 消</el-button>
      <el-button type="primary" @click="submitAddForm('addForm')">确 定</el-button>
    </div>
  </el-dialog>
  <el-dialog title="修改" :visible.sync="editFormVisible">
    <el-form :model="editForm"  ref="editForm"  :rules="rules" :inline="true"  class="demo-form-inline line-form">
      <el-form-item   label="费用科目" :label-width="formLabe2Width" prop="subject">
        <el-input v-model="editForm.subject"  auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item   label="金额" type="number" :label-width="formLabe2Width" prop="money">
        <el-input v-model="editForm.money"  auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item   label="发生时间"   :label-width="formLabe2Width" prop="date">
        <el-date-picker type="date" @change="dateChangeEdit" placeholder="选择日期" v-model="editForm.date" style="width: 100%;"></el-date-picker>
      </el-form-item>
      <el-form-item   label="备注" :label-width="formLabe2Width" >
        <el-input v-model="editForm.remark"  auto-complete="off"></el-input>
      </el-form-item>

    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="editFormVisible = false">取 消</el-button>
      <el-button type="primary" @click="submitEditForm('editForm')">确 定</el-button>
    </div>
  </el-dialog>
</div>

</template>

<script>
  import ElRow from "element-ui/packages/row/src/row";
  import ElCol from "element-ui/packages/col/src/col";
  /*导入配置文件*/
  import config from "../assets/configVue"
export default {
  components: {
    ElCol,
    ElRow},
  name: 'apply',
  data () {
    let checkMoney = (rule,value,callback)=>{
        if(!value){
            return callback(new Error('金额不能为空！'))
        }
        let rep = /(^[1-9](\d+)?(\.\d{1,2})?$)|(^(0){1}$)|(^\d\.\d{1,2}?$)/;
      setTimeout(() => {
        if (!rep.test(value)) {
          callback(new Error('请输入数字保留两位小数！'));
        } else {
            callback();
        }
      }, 300);
    };
    return {
      basicForm:{
        theme: '',
        number:'',
        dep: ''
      },
      tableData:[],
      addForm:{
        subject:'',
        money:'',
        date: '',
        remark: ''
      },
      addFormVisible:false,
      editForm:{},
      editFormVisible:false,
      labelWidth:'160px',
      formLabe2Width:'180px',
      rules:{
        theme: [
          { required: true, message: '请输入费用主题', trigger: 'blur' }
        ],
        number: [
          { required: true, message: '请输入费用编号', trigger: 'blur' }
        ],
        dep: [
          { required: true, message: '请输入归属部门', trigger: 'blur' }
        ],
        subject:[
          { required: true, message: '请选择费用科目', trigger: 'blur' }
        ],
        money:[
          { validator: checkMoney,required:true, trigger: 'blur' }
        ],
        date: [
          { required: true, message: '请选择日期' }
        ],
      },
      editIndex:0,
      total:0
    }
  },
  created(){
      var reg = new RegExp("(^|&)" + "pocCode" + "=([^&]*)(&|$)", "i");
      var r = window.location.search.substr(1).match(reg);
      var pocCode;
      var self=this;
      if (r != null){
        pocCode=unescape(r[2]);
        //alert(pocCode);
      }
      if(pocCode!=""&&pocCode!=undefined){
        this.$http.get(config.url+config.portC+"busPoc?code="+pocCode).then(function(data){
          //alert(data.body.title);
          self.basicForm.theme=data.body.title;
          self.basicForm.number=data.body.code;
          self.basicForm.dep=data.body.department;
          self.tableData=data.body.detail;
        },function(response){
          console.info(response);
          if(response){
          }
        });
      }
  },
  watch:{
    tableData:{
      handler: function (val, oldVal) {
          let self = this;
          this.total = 0;
          for(let i = 0;i < self.tableData.length;i++){
            this.total += parseFloat(this.tableData[i].money);
          }
        console.log(this.total)
      },
      deep: true
    }
  },
  methods:{
    add:function () {
      this.addFormVisible=true
    },
    edit:function (row,index) {
      this.editFormVisible = true;
      this.editIndex = index;
      this.editForm = Object.assign({},this.tableData[index])
    },
    deleteDta:function (row,index) {
      this.tableData.splice(index,1);

    },
    submit:function (formName) {
      var self=this;
      var processDefinitionKey=getQueryString("processDefinitionKey");
      var pocCode=getQueryString("pocCode");
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(pocCode!=''&&pocCode!=undefined){
            var taskId=getQueryString("taskId");
            this.$http.post(config.url+config.portP+"runtime/tasks/"+taskId,{"action" : "complete"}).then(function(data){
              window.location.href=config.url+config.portC+"businessProcessManager/html/run-task.html";
            });
            return;
          }
          self.$http.post(config.url+config.portC+"busPoc",{"title":self.basicForm.theme,"code":self.basicForm.number,"department":self.basicForm.dep,
           "detail":self.tableData}).then(function(data){
            self.$http.post(config.url+config.portP+"runtime/process-instances",{"processDefinitionKey":processDefinitionKey,
              "businessKey":data.data}).then(function(data){
              var result=data.data.id;
              var message;
              if(result!=null){
                message="启动成功";
              }else {
                message="启动失败";
              }
              this.$message({
                message: message,
                type: 'warning'
              });
              window.location.href=config.url+config.portC+"businessProcessManager/html/run-task.html";
            })
          });
          //alert('提交成功');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    pass:function () {
      var pocCode=getQueryString("pocCode");
      if(pocCode!=''&&pocCode!=undefined){
        var taskId=getQueryString("taskId");
        this.$http.post(config.url+config.portP+"runtime/tasks/"+taskId,{
          "action" : "complete",
          "variables" : [
            {
              "name" : "pass",
              "value" : true,
              "scope" : "local",
              "type" : "boolean"
            }
          ]
        }).then(function(data){
          window.location.href=config.url+config.portC+"businessProcessManager/html/run-task.html";
        });
      }
    },
    nopass:function () {
      var pocCode=getQueryString("pocCode");
      if(pocCode!=''&&pocCode!=undefined){
        var taskId=getQueryString("taskId");
        this.$http.post(config.url+config.portP+"runtime/tasks/"+taskId,{
          "action" : "complete",
          "variables" : [
            {
              "name" : "pass",
              "value" : false,
              "scope" : "local",
              "type" : "boolean"
            }
          ]
        }).then(function(data){
          window.location.href=config.url+config.portC+"businessProcessManager/html/run-task.html";
        });
      }
    },
    submitAddForm:function (formName) {
      let self = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          self.addFormVisible = false;
          self.tableData.push(self.addForm);
          self.addForm = Object.assign({},{
            subject:'',
            money:'',
            date: '',
            remark: ''
          })
//          self.tableData.pop({});
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    submitEditForm:function (formName) {
      let self = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          self.editFormVisible = false;
          self.tableData[self.editIndex] = Object.assign({},self.editForm);
          console.log(self.editForm)
          self.tableData.push({});
          self.tableData.pop({});
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    dateChangeAdd:function (val) {
        this.addForm.date = val;
    },
    dateChangeEdit:function (val) {
        this.editForm.date = val;
    }
  }
}
  function getQueryString(name) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    var r = window.location.search.substr(1).match(reg);
    if (r != null) return unescape(r[2]); return null;
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >
.log-input .el-form-item__content{
  width: 240px;
}
.line-form .el-form-item__content{
  width: 192px;
}
.log-input .el-form-item{
  margin-top: 20px;
  margin-bottom: 40px;
}
  .little-nav{
    padding: 10px 0;
    font-weight: 600;
  }
  .text-right{
    text-align: right;
  }
  .text-center,.text-center th{
    text-align: center;
  }
  .btn-box{
    margin-top: 100px;
    margin-bottom: 20px;
  }
.el-table td{
  height: 50px;
}
  .total-money{
    line-height: 50px;
  }
</style>



